<template>
    <div class="map">
    <div class="search_map">
      <router-link class="back_btn" to="/index" v-if="!isIOS"></router-link>
      <div id="map"></div>
    </div>
  </div>
</template>
<script>
import { getnotice } from "../common";
export default {
  data() {
    return {
      isIOS: false,
      mapMsg: this.$route.query
    };
  },
  created() {
    this.showMap();
  },
  mounted() {
    var isMobile = {
      Android : function() {
        return navigator.userAgent.match(/Android/i) ? true : false;
      },
      BlackBerry : function() {
        return navigator.userAgent.match(/BlackBerry/i) ? true : false;
      },
      iOS : function() {
        return navigator.userAgent.match(/iPhone|iPad|iPod/i) ? true : false;
      },
      Windows : function() {
        return navigator.userAgent.match(/IEMobile/i) ? true : false;
      },
      any : function() {
        return (isMobile.Android() || isMobile.BlackBerry() || isMobile.iOS() || isMobile.Windows());
      }
    };
    if (isMobile.iOS()) {
      this.isIOS= true;
    } else {
      this.isIOS=false;
    }
  },
  methods: {
    showMap() {
      var map;
      var _t = this;
      console.log(_t.mapMsg, "-----");
      if (_t.mapMsg.lat && _t.mapMsg.lng) {
        setTimeout(function() {
          initMap();
        }, 100);
      } else {
        getnotice("未获取到酒店坐标");
        setTimeout(function() {
          _t.$router.push("/index");
        }, 1000);
      }

      function initMap() {
        createMap(); //创建地图
        setMapEvent(); //设置地图事件
        addMapControl(); //向地图添加控件
        addMapOverlay(); //向地图添加覆盖物
      }
      function createMap() {
        console.log(_t.mapMsg.lng, _t.mapMsg.lat);
        map = new BMap.Map("map");
        map.centerAndZoom(new BMap.Point(_t.mapMsg.lng, _t.mapMsg.lat), 15);
      }
      function setMapEvent() {
        map.enableScrollWheelZoom();
        map.enableKeyboard();
        map.enableDragging();
        map.enableDoubleClickZoom();
      }
      function addClickHandler(target, window) {
        target.addEventListener("click", function() {
          target.openInfoWindow(window);
        });
      }
      function addMapOverlay() {
        var markers = [
          {
            content: "地址:" + _t.mapMsg.address,
            title: _t.mapMsg.hotelName,
            imageOffset: { width: -46, height: -21 },
            position: { lat: _t.mapMsg.lat, lng: _t.mapMsg.lng }
          }
        ];
        for (var index = 0; index < markers.length; index++) {
          var point = new BMap.Point(
            markers[index].position.lng,
            markers[index].position.lat
          );
          var marker = new BMap.Marker(point, {
            icon: new BMap.Icon(
              "http://api.map.baidu.com/lbsapi/createmap/images/icon.png",
              new BMap.Size(20, 25),
              {
                imageOffset: new BMap.Size(
                  markers[index].imageOffset.width,
                  markers[index].imageOffset.height
                )
              }
            )
          });
          var label = new BMap.Label(markers[index].title, {
            offset: new BMap.Size(25, 5)
          });
          var opts = {
            width: 200,
            title: markers[index].title,
            enableMessage: false
          };
          var infoWindow = new BMap.InfoWindow(markers[index].content, opts);
          marker.setLabel(label);
          addClickHandler(marker,infoWindow);
          map.addOverlay(marker);
          marker.openInfoWindow(infoWindow);
        }
      }
      //向地图添加控件
      function addMapControl() {
        var navControl = new BMap.NavigationControl({
          anchor: BMAP_ANCHOR_TOP_LEFT,
          type: BMAP_NAVIGATION_CONTROL_LARGE
        });
        map.addControl(navControl);
      }
    }
  },
  watch: {}
};
</script>

<style scoped>
.map{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.map .search_map{
  width: 100%;
  height: 100%;
}

#map {
  width: 100%;
  height: 100%;
}

.BMap_stdMpCtrl {
  display: none !important;
}

.restric .f-model-card-body {
  padding-top: 0rem !important;
}

.restric .f-model-card-foot {
  color: #3f3f3f !important;
}
</style>



